<template>
  <div class="content">
    <div class="row">
      <div class="col-3" v-for="(item, i) in htmls">
        <card
        type="chart">
          <h4 class="card-title">{{ item }}</h4>
          <a :href="'#/dashboard/'+i" class="btn btn-primary">Go</a>
        </card>
      </div>
    </div>
  </div>
</template>
<script>

import {
  Card
} from "@/components/index";

import LineChart from '@/components/Charts/LineChart';
import BarChart from '@/components/Charts/BarChart';
import * as chartConfigs from '@/components/Charts/config';
import TaskList from './Dashboard/TaskList'
import UserTable from './Dashboard/UserTable'
import config from '@/config';

export default {
  components: {
    Card,
    LineChart,
    BarChart,
    TaskList,
    UserTable
  },
  data(){
    return{
      htmls: ['William Tyndale(1494-1536)', 'William Carey(1761-1834)', 'John Calvin(1509-1564)', 'William Booth(1829-1912)', 'Jonathan Goforth(1859-1936)', 'R. A. Torrey(1856-1927)', 'C. H. Spurgeon(1834-1891)', 'A. Augustinus(354-430)', 'John Wycliffe(1328-1384)', 'John Song(1901-1944)', 'A. B. Simpson(1843-1919)', 'J. Penn-Lewis(1832-1905)', 'G. Whitefield(1714-1769)', 'George Muller(1805-1898)', 'Andrew Murray(1828-1917)', 'D. L. Moody(1837-1899)', 'J. Hudson Taylor(1832-1905)', 'G. C. Morgan(1863-1945)', 'John Bunyan(1627-1688)', 'Charles Wesley(1707-1788)', 'J. Edwards(1703-1758)', 'Ming Dao Wang(1900-1991)', 'Madame Guyon(1648-1717)', '.John Newton(1725-1807)', 'John Knox(1505-1572)', 'John Wesley(1703-1791)', 'E. Roberts(1878-1950)', 'F. Crosby(1820-1915)', 'C. Finney(1792-1843)', 'C. N. Zinzendorf(1700-1760)', 'J. N. Darby(1800-1882)', 'F.B.Meyer(1847-1929)', 'D. M. Lloyd-Jones(1899-1981)', 'A.W. Tozer(1897-1963)', 'Martin Luther(1483-1546)']
  }
  },
  computed:{
    enableRTL() {
      return this.$route.query.enableRTL;
    },
    isRTL(){
      return this.$rtl.isRTL;
    }
  },
  methods:{
  },
  mounted(){
    this.i18n = this.$i18n;
    if (this.enableRTL) {
      this.i18n.locale = 'ar';
      this.$rtl.enableRTL();
    }
  },
  beforeDestroy() {
    if (this.$rtl.isRTL) {
      this.i18n.locale = 'en';
      this.$rtl.disableRTL();
    }
  }
}
</script>
